.modal {

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    overflow: hidden;
    overflow-y: auto;
    transition: opacity 0.3s;
    z-index: 999;

    &__wrapper {

        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100%;

        .modal__formRegistration {

            position: relative;
            width: 100%;
            max-width: 600px;
            border-radius: 20px;
            background-color: #fff;
            padding: 40px 20px;
            transform: translateY(-50px);
            transition: opacity 0.3s, transform 0.3s;
            display: flex;
            flex-direction: column;
            align-items: center;

            .error {
                margin-bottom: 15px;
                font-size: 20px;
                color: burlywood;
            }

            h2 {
                margin-bottom: 20px;
            }

            input {

                margin-bottom: 40px;
                height: 40px;
                padding: 10px;
                color: darkgray;
                font-size: 16px;
            }

            .modal__create-btn {

                width: 200px;
                height: 40px;
                background-color: #80233a;
                color: #fff;
                border: none;
                font-size: 18px;
                cursor: pointer;

            }

            .modal__close-btn {

                position: absolute;
                top: 16px;
                right: 16px;
                width: 30px;
                height: 30px;
                padding: 0;
                background-color: transparent;
                border: none;
                cursor: pointer;
                transition: 0.3s;
                font-size: 22px;
            }

            .modal__close-btn:hover {

                color: #80233a;
            }
        }
    }
}